import React, { Component } from 'react';
import './styles.css';
import PartnersList from '../partnersList/partnersList.js';

class Partners extends Component {
    render() {
      const { 
        teamData: { name, logo, partners }
      } = this.props;
      console.log(logo);
      return (
        <div className="col-12 d-flex partners px-0">
          <div className="d-none d-md-flex flex-column col-md-2 justify-content-center align-items-center p-1 b-red team-logo">        
            <img style={{opacity: "0.7"}} width="100" src={logo} alt={name} />
            <h1 className="text-center">{name}</h1>
          </div>
          <PartnersList
            partners={partners}
          />
        </div>
      )
    }
  }

  export default Partners;