import React, { Component } from 'react';
import './styles.css';
import countries from '../../api/countries.js';
import Field from '../../components/Field';

class AboutContacts extends Component {
  constructor(props) {
    super(props);

    this.state = {
      username: "",
      password: "",
      repeatPassword: "",
      country: "1",
      gender: "male",
      agreePp: true,
      agreeToU: true,
      avatar: "",
      age: 0,
      errors: {
        username: false,
        password: false,
        repeatPassword: false,
        age: false,
      },
    }
  };

  onChange = (event) => {
    this.setState({
      [event.target.name]: event.target.value,
    })
  };

  onChangeCheckbox = (event) => {
    console.log(event.target.name, event.target.value, event.target.checked);
    this.setState({
      [event.target.name]: event.target.checked,
    })
  };

  // конвертируем загружаемую картинку в формат В64
  // чтобы передать на сервер и использовать как src, 
  // также для загрузки файлов в INPUT не нужно указывать VALUE 
  onChangeAvatar = (event) => {
    const reader = new FileReader();
    reader.onload = (event) => {
      this.setState({
        avatar: event.target.result
      })
    };

    reader.readAsDataURL(event.target.files[0]);
  };

  onSubmit = (event) => {
    event.preventDefault();

    const errors = {};
    if (this.state.username.length < 5) {
      errors.username = "Must be 5 characters or more";
    }
    if (this.state.password < 3) {
      errors.password = "Must be 3 characters or more";
    }
    if (this.state.password !== this.state.repeatPassword) {
      errors.repeatPassword = "Must be equal password";
    }

    if(Object.keys(errors).length > 0) {
      this.setState({
        errors: errors
      });
    } else {
      this.setState({
        errors: {}
      });
      console.log("submit", this.state);
    }
  };

  getOptionsItems = (items) => {
    return items.map(item=> (
      <option key={item.id} value={item.id}>
        {item.name}
      </option>
    ));
  };

  incrementAge = () => {
    this.setState(
      prevState => ({
        age: prevState.age + 1 
      }),
      () => {
        if (this.state.age < 3) {
          this.setState({
            errors: {
              age: "Must be more then 3 years"
            }
          });
        } else {
          this.setState({
            errors: {
              age: false
            }
          });
        }
        console.log("Age", this.state.age)
      }
    );
  };
  
  decrementAge = () => {
    this.setState(
      prevState => ({
        age: prevState.age - 1 
      }),
      () => {
        if (this.state.age < 3) {
          this.setState({
            errors: {
              age: "Must be more then 3 years"
            }
          });
        } else {
          this.setState({
            errors: {
              age: false
            }
          });
        }
        console.log("Age", this.state.age)
      }
    );
  };

  render() {
    return (
      <div className="contacts py-4">
        <div className="form-container card">
          <form className="form card-body">
            <Field
              id="username"
              labelText="User name" 
              type="text" 
              placeholder="Enter user name" 
              name="username"
              value={this.state.username}
              onChange={this.onChange}
              error={this.state.errors.username}
            />
            <Field
              id="password"
              labelText="Password" 
              type="password" 
              placeholder="Enter password" 
              name="password"
              value={this.state.password}
              onChange={this.onChange}
              error={this.state.errors.password}
            />
            <Field
              id="repeatPassword"
              labelText="Password" 
              type="password" 
              placeholder="Enter repeat password" 
              name="repeatPassword"
              value={this.state.repeatPassword}
              onChange={this.onChange}
              error={this.state.errors.repeatPassword}
            />
            <div className="form-group">
              <label htmlFor="country">Country</label>
              <select 
                className="custom-select"
                id="country"
                name="country"
                value={this.state.country}
                onChange={this.onChange}
              >  
                {this.getOptionsItems(countries)}
              </select>
            </div>
            <fieldset className="form-group">
              <div>Gender</div>
              <div className="custom-control custom-radio">
                <input 
                  className="custom-control-input" 
                  type="radio" 
                  id="male" 
                  name="gender"
                  value="male"
                  checked={this.state.gender === "male"}
                  onChange={this.onChange}
                />
                <label className="custom-control-label" htmlFor="male">
                  Male
                </label>
              </div>
              <div className="custom-control custom-radio">
                <input 
                  className="custom-control-input" 
                  type="radio" 
                  id="female" 
                  name="gender"
                  value="female"
                  checked={this.state.gender === "female"}
                  onChange={this.onChange}
                />
                <label className="custom-control-label" htmlFor="female">
                  Female
                </label>
              </div>
            </fieldset>
            <div className="form-group">
              <label htmlFor="avatar">Avatar</label>
              <input 
                type="file" 
                className="form-control-file" 
                id="avatar" 
                name="avatar"
                onChange={this.onChangeAvatar}  
              />
            </div>
            <div className="form-group">
              <div>
                <label>Age</label>
              </div>
              <div className="btn-group">
                <button 
                  className="btn btn-secondary"
                  type="button"
                  onClick={this.decrementAge}  
                >
                  -
                </button>
                <input
                  type="number"
                  className="form-control"
                  placeholder="Enter age"
                  name="age"
                  value={this.state.age}
                  onChange={this.onChange}
                />
                <button 
                  className="btn btn-secondary"
                  type="button"
                  onClick={this.incrementAge}  
                >
                  +
                </button>
              </div>
              {this.state.errors.age ? (
                <div className="invalid-feedback">
                  {this.state.errors.age}
                </div>
              ) : null }
            </div>
            <div className="custom-control custom-checkbox mb-2">
              <input 
                className="custom-control-input" 
                type="checkbox"
                id="agreePp"
                name="agreePp"
                value={this.state.agreePp}
                onChange={this.onChangeCheckbox}
                checked={this.state.agreePp === true}   
              />
              <label className="custom-control-label" htmlFor="agreePp">
                Agree with Privacy policy
              </label>
            </div>
            <div className="custom-control custom-checkbox mb-2">
              <input 
                className="custom-control-input" 
                type="checkbox"
                id="agreeToU"
                name="agreeToU"
                value={this.state.agreeToU}
                onChange={this.onChangeCheckbox}
                checked={this.state.agreeToU === true}   
              />
              <label className="custom-control-label" htmlFor="agreeToU">
                Agree with Terms of use
              </label>
            </div>
            <button 
              type="submit" 
              className="btn btn-primary w-100"
              onClick={this.onSubmit}
            >
              Submit
            </button>
          </form>
        </div>
      </div>
    )
  }
}

export default AboutContacts;