import React, { Component } from 'react';
import './styles.css';

class BigItemOnPage extends Component {
  render() {
    const { item, showBigImage, toggleShowBigImage, title } = this.props;
    return(
      <div className="block" style={{ display: showBigImage}} onClick={()=> {toggleShowBigImage()}}>
        <div className="block-bg"></div>
        <div className="bigItem">
          <img className="bigImage" src={(item !== null) ? item : 'logo' } alt={title} /> 
        </div>
      </div>
    )
  }
}

export default BigItemOnPage;