import React, { Component } from 'react';
import './styles.css';
//import logo from '../../assets/img/logo/logo-odessa.png';

class PlayerCard extends Component {
  render() {
    const { data } = this.props;
    return (
      <div className="playerCard">
        <div className="shadowBlock"></div>
        <div className="playerCard-top">
          <div className="playerCard-top-left">top</div>
          <div className="playerCard-top-center">top</div>
          <div className="playerCard-top-right">top</div>
        </div>
        <div className="playerCard-content">content</div>
        <div className="playerCard-bottom">
          <div className="playerCard-bottom-left">bottom-right</div>
          <div className="playerCard-bottomArrow">
            <div className="playerCard-bottomArrow-left"></div>
            <div className="playerCard-bottomArrow-right"></div>
          </div>
          <div className="playerCard-bottom-right">bottom-right</div>
        </div>
      </div>
    )
  }
}

export default PlayerCard;